import React from 'react';
import { vendorText } from '../../../config/text';

export default function Vendor(vendor, availableSlot, classes, onClick) {
  const vendorBg = {
    background: 'url(' + vendor.iconUrl + ') no-repeat center center',
  };

  return (
    <div
      key={vendor.id}
      className={classes.vendor}
      style={vendorBg}
      onClick={() => availableSlot && onClick(vendor.id)}
    >
      <h3 className={classes.vendorTime}>
        {availableSlot
          ? `${vendorText.availableTime} ${availableSlot}`
          : 'Замовлення недоступне'}
      </h3>
      <h2 className={classes.vendorTitle}>{vendor.name}</h2>
      <h2 className={classes.vendorDescription}>{vendor.description}</h2>
    </div>
  );
}
