import React, { Fragment, useState } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import NoProductImg from '../img/no-product-photo.png';

import tileData from '../common/menuData';
import ProductView from '../common/ProductView';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: 'auto',
    maxWidth: 500,
  },
  image: {},
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
}));

function Menu() {
  const classes = useStyles();

  return (
    <div className={classes.page}>
      {
        <List cellHeight={180} cols={1} className={classes.gridList}>
          {tileData.map((tile) => ProductView(tile))}
        </List>
      }
    </div>
  );
}

export default withRouter(Menu);
