import { GET_TIME_SLOTS_SUCCESS, GET_TIME_SLOTS } from '../actions';
import { head } from 'ramda';

const initialState = {
    list: [],
    available: null
};

export default function reducer(state = initialState, action = {}) {
    switch (action.type) {
        case GET_TIME_SLOTS:
            return { ...state, list: [], available: null };
        case GET_TIME_SLOTS_SUCCESS:
            return { ...state, list: action.data, available: head(action.data) };
        default:
            return state;
    }
}
