import makeActionCreator from '../../utils/makeActionCreator';

export const SET_TO_CART_REQUEST = 'SET_TO_CART_REQUEST';
export const REMOVE_FROM_CART_REQUEST = 'REMOVE_FROM_CART_REQUEST';
export const SET_TO_CART_SUCCESS = 'SET_TO_CART_SUCCESS';
export const RESET_CART = 'RESET_CART';
export const PLACE_ORDER_REQUEST = 'PLACE_ORDER_REQUEST';
export const PLACE_ORDER_SUCCESS = 'PLACE_ORDER_SUCCESS';
export const PLACE_ORDER_ERROR = 'PLACE_ORDER_ERROR';
export const setToCartRequest = makeActionCreator(
  SET_TO_CART_REQUEST,
  'product',
);
export const removeFromCartRequest = makeActionCreator(
  REMOVE_FROM_CART_REQUEST,
  'product',
);
export const setToCartSuccess = makeActionCreator(SET_TO_CART_SUCCESS, 'data');
export const resetCart = makeActionCreator(RESET_CART);

export const placeOrderRequest = makeActionCreator(
  PLACE_ORDER_REQUEST,
  'timeSlot',
  'callback',
);
export const placeOrderSuccess = makeActionCreator(PLACE_ORDER_SUCCESS);
export const placeOrderError = makeActionCreator(PLACE_ORDER_ERROR, 'error');
