import { makeStyles } from '@material-ui/core/styles';

import colors from '../../../themes';
import { lighten } from '../../../themes/utility';

const useStyles = makeStyles((theme) => ({
  bucketButton: {
    height: '52px',
    width: '156px',
    padding: '10px 0px',
  },
  amountBtnContainer: {
    display: 'flex',
    // width: '165px',
    color: colors.primary,
    '& button': {
      height: '52px',
      width: '52px',
      fontSize: '30px',
      backgroundColor: colors.third,
      color: colors.white,
      boxShadow: '0px 3px 10px rgba(245, 172, 81, 0.3)',
      border: 'none',
      '&:hover': {
        backgroundColor: lighten(colors.third, 0.1),
      },
    },
  },
  amountBtnMinus: {
    borderTopLeftRadius: '5px',
    borderBottomLeftRadius: '5px',
    paddingBottom: '5px',
  },
  amountBtnCount: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '52px',
    width: '52px',
    fontSize: '20px',
    fontWeight: 'bold',
  },
  amountBtnPlus: {
    borderTopRightRadius: '5px',
    borderBottomRightRadius: '5px',
  },
}));

export default useStyles;
