import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_VENDORS,
  getVendorsLstSuccess,
  getProductsListFail,
  openModal,
} from '../store/actions';
import { getVendors } from '../api/Vendors';

export function* fetchData(action) {
  try {
    const { vendors } = yield call(getVendors);
    console.log('data', vendors);
    yield put(getVendorsLstSuccess(vendors));
  } catch (error) {
    yield put(openModal('Error', JSON.stringify(error)));
    yield put(getProductsListFail(error));
  }
}

export function* vendorsSaga() {
  yield takeEvery(GET_VENDORS, fetchData);
}
