import {
  GET_PRODUCTS_BY_VENDOR,
  GET_PRODUCTS_BY_VENDOR_SUCCESS,
  GET_PRODUCTS_BY_VENDOR_FAIL,
} from '../actions';

const initialState = {
  loading: false,
  list: [],
  error: null,
};

export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case GET_PRODUCTS_BY_VENDOR:
      return { ...state, list: [], loading: true };
    case GET_PRODUCTS_BY_VENDOR_SUCCESS:
      return { ...state, list: action.data, error: null, loading: false };
    case GET_PRODUCTS_BY_VENDOR_FAIL:
      return { ...state, list: null, loading: false, error: action.error };
    default:
      return state;
  }
}
