import { MODAL_IS_OPEN, MODAL_IS_CLOSE } from '../actions';

const initialState = {
  open: false,
  title: null,
  text: null,
};

export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case MODAL_IS_OPEN:
      return { ...state, open: true, title: action.title, text: action.text };
    case MODAL_IS_CLOSE:
      return { ...state, open: false, title: null, text: null };
    default:
      return state;
  }
}
