import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { useHistory, withRouter } from 'react-router-dom';
import clsx from 'clsx';

import { ReactComponent as HeaderLogo } from '../../img/svg/logo.svg';
import { ReactComponent as BucketIcon } from '../../img/svg/icons/bucket.svg';
import UserMenu from './components/UserMenu';
import PaperButton from '../../common/components/Button';
import { userLogOut } from '../../store/actions/userAuth';
import { getProductsFromCart } from '../../store/selectors/cart';
import { getCartTotalPrice } from '../../utils/helpers';
import { ROLES } from '../../config';
import { header } from '../../config/text';
import { useStyles } from './styles';

const Header = ({ userInfo, cartItems, actions: { userLogOut } }) => {
  const history = useHistory();
  const navigateToVendors = () => history.push('/vendors');
  const navigateToCart = () => history.push('/cart');

  const classes = useStyles();

  const cartTotalPrice = getCartTotalPrice(cartItems);

  const renderAdminButton = () => {
    const { location } = history;
    const isAdminPage = location.pathname.includes('/admin');
    const buttonLabel = isAdminPage ? 'В каталог' : 'До замовлень';

    return (
      <PaperButton
        className={classes.adminButton}
        onClick={() => {
          const redirectionTo = isAdminPage ? '/vendors' : '/admin';
          history.push(redirectionTo);
        }}
      >
        {buttonLabel}
      </PaperButton>
    );
  };

  return (
    <div className={classes.wrapper}>
      <div className={classes.container}>
        <div className={classes.logoWrapper}>
          <HeaderLogo
            style={{ fontSize: 1, cursor: 'pointer' }}
            onClick={navigateToVendors}
          />
          <span className={classes.title}>{header.title}</span>
        </div>
        {userInfo ? (
          <div className={classes.headerItems}>
            <div className={classes.headerItem}>
              {userInfo.role === ROLES.SUPER && renderAdminButton()}
            </div>
            <div className={classes.headerItem}>
              <span>{header.yourBalance}:</span>
              <span className={classes.balance}>
                {Number.isNaN(Number(userInfo.balance)) ? 0 : userInfo.balance}₴
              </span>
            </div>
            <div
              className={clsx(
                classes.headerItem,
                cartTotalPrice > 0 ? classes.bucket : {},
              )}
              onClick={cartTotalPrice > 0 ? navigateToCart : null}
            >
              <BucketIcon />
              <span>{getCartTotalPrice(cartItems)} ₴</span>
            </div>
            <div className={classes.headerItem}>
              <UserMenu userLogOut={userLogOut} userEmail={userInfo.email} />
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  userInfo: state.user.userData,
  cartItems: getProductsFromCart(state),
});

const mapDispatchToProps = (dispatch) => ({
  actions: bindActionCreators(
    {
      userLogOut,
    },
    dispatch,
  ),
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header));
