import api, { getAuthToken } from '../utils/api';
import { logIn, userInfoUrl, userRegister } from '../config/api';
import { removeTokenFromStorage } from '../utils/helpers';

export const getFullUserInfo = (token) => api.get(userInfoUrl, null, token);

export const callLogin = (email, password, rememberMe = null) => {
  return getAuthToken(email, password);
};

export const register = (email, password) => {
  const data = {
    email,
    password,
  };

  return api.post(userRegister, data, null, '');
};
