import React from 'react';
import clsx from 'clsx';

import colors from '../../../../themes';
import { cart as cartTranslations } from '../../../../config/text';

import useStyles from './styles';

export default function StatusCard({ success }) {
  const classes = useStyles();

  return (
    <div
      className={clsx(
        classes.card,
        success ? classes.successStatus : classes.pendingStatus,
      )}
    >
      {success ? 'Виконано' : 'Замовлено'}
    </div>
  );
}
