import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { SvgIcon } from '@material-ui/core';

import { ReactComponent as SelectorTopIcon } from '../../../img/svg/icons/selector_top.svg';
import { ReactComponent as SelectorBottomIcon } from '../../../img/svg/icons/selector_bottom.svg';
import colors from '../../../themes';
import { lighten } from '../../../themes/utility';

const useStyles = makeStyles((theme) => ({
  button: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    border: `1px solid ${colors.primaryLight}`,
    background: colors.white,
    boxSizing: 'border-box',
    borderRadius: '20px',
    width: 'fit-content',
    color: colors.secondary,
    fontSize: '11px',
    fontWeight: 'bold',
    padding: '5px 11px',
  },
  iconWrapper: {
    height: 7,
    width: 11,
    marginLeft: '7px',
  },
  icon: {
    fontSize: '18px',
  },
}));

const PaperExpander = ({ expanded, label, className, onClick }) => {
  const classes = useStyles();

  return (
    <button className={clsx(classes.button, className)} onClick={onClick}>
      {label}
      <div className={classes.iconWrapper}>
        <SvgIcon
          className={classes.icon}
          component={expanded ? SelectorTopIcon : SelectorBottomIcon}
        />
      </div>
    </button>
  );
};

export default PaperExpander;
