import React, { Fragment, useState } from 'react';
import { withRouter, useHistory } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import {
  getAllUsers,
  getTodayOrders,
  addBalanceToUser,
  setStatusToUser,
} from '../../api/admin';

import { openModal } from '../../store/actions';
import { ROLES } from '../../config';
import PaperInput from '../../common/components/Input';
import PaperBackButton from '../../common/components/BackButton';
import PaperButton from '../../common/components/Button';
import PaperSpinner from '../../common/components/Spinner';

import StatusCard from './components/StatusCard';
import { balanceConfirmStatusMap } from './constants';
import useStyles from './styles';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role='tabpanel'
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

const rows = [
  { id: 1, name: 'Test', balance: 200 },
  { id: 2, name: 'Test 2', balance: 200 },
  { id: 3, name: 'Test 3', balance: 200 },
];

const rows1 = [{ id: 1, name: 'Noa', time: '19:00', status: false }];

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function AdminPage({ user: { token, userData }, openModal }) {
  const classes = useStyles();
  const history = useHistory();
  const [value, setValue] = React.useState(0);
  const [users, setUsers] = React.useState([]);
  const [vendors, setVendors] = React.useState([]);
  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    if (!token) {
      history.push({
        pathname: '/checkout-status',
        state: { status: 'error' },
      });
    }

    (async function () {
      try {
        setLoading(true);
        const { users } = await getAllUsers(token);
        const { vendorOrders } = await getTodayOrders(token);
        console.log('allUsers', users, vendorOrders);
        setUsers(users);
        setVendors(vendorOrders.length ? vendorOrders : rows1);
        setLoading(false);
      } catch (error) {
        console.log('Admin error: ', error);
        setLoading(false);
        history.push({
          pathname: '/checkout-status',
          state: { status: 'error' },
        });
      }
    })();
  }, []);

  if (loading) return <PaperSpinner />;

  const setActiveUser = (userId) => {
    setUsers(
      users.map((user) => ({
        ...user,
        active: user.id === userId,
      })),
    );
  };

  const onUserBalanceChange = (userId, balance) => {
    setUsers(
      users.map((user) => ({
        ...user,
        balance: user.id === userId ? balance : user.balance,
        balanceConfirmStatus:
          user.id === userId
            ? balanceConfirmStatusMap.pending
            : user.balanceConfirmStatus,
      })),
    );
  };

  const onUserBalanceConfirm = async (email, balance) => {
    try {
      const changedUserResponse = await addBalanceToUser(email, balance, token);
      const changedUser = {
        ...changedUserResponse,
        active: false,
        balanceConfirmStatus: undefined,
      };

      setUsers(
        users.map((user) => (user.email === email ? changedUser : user)),
      );
    } catch (error) {
      console.log('Change balance error: ', error);
      openModal('Change balance error', JSON.parse(error).join(', '));
    }
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const setRoleToUser = async (email) => {
    try {
      const changedUserResponse = await setStatusToUser(
        token,
        email,
        ROLES.CASUAL,
      );

      const changedUser = {
        ...changedUserResponse,
        active: false,
        balanceConfirmStatus: undefined,
      };

      setUsers(
        users.map((user) => (user.email === email ? changedUser : user)),
      );
    } catch (error) {
      console.log('Change status error: ', error);
      openModal('Change status error', error);
    }
  };

  return (
    <div className={classes.page}>
      <PaperBackButton />
      <div className={classes.wrapper}>
        <div className={classes.content}>
          <div className={classes.tabs}>
            <Tabs
              value={value}
              onChange={handleChange}
              classes={{
                label: classes.tabTitle,
                indicator: classes.tabIndicator,
              }}
              className={classes.tab}
            >
              <Tab label='Користувачі' {...a11yProps(0)} />
              <Tab label='Замовлення' {...a11yProps(1)} />
            </Tabs>
          </div>
          <TabPanel value={value} index={0}>
            <TableContainer>
              <Table className={classes.table} aria-label='simple table'>
                <TableHead>
                  <TableRow>
                    <TableCell>Користувач</TableCell>
                    <TableCell>Роль</TableCell>
                    <TableCell align='right'>Баланс</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {users.map(
                    ({
                      id,
                      email,
                      role,
                      balance,
                      active,
                      balanceConfirmStatus,
                    }) => (
                      <TableRow key={id} className={classes.tableRow}>
                        <TableCell
                          component='th'
                          scope='row'
                          className={classes.tabCellName}
                        >
                          {email}
                        </TableCell>
                        <TableCell
                          component='th'
                          scope='row'
                          className={classes.tabCellName}
                        >
                          {role === ROLES.CSIA_PENDING ||
                          role === ROLES.EMAIL_PENDING ? (
                            <PaperButton
                              className={classes.confirmUserButton}
                              onClick={() => setRoleToUser(email)}
                            >
                              Підтвердити
                            </PaperButton>
                          ) : (
                            role
                          )}
                        </TableCell>
                        <TableCell onClick={() => setActiveUser(id)}>
                          {active ? (
                            <PaperInput
                              value={balance}
                              containerStyle={classes.inputContainer}
                              inputClass={classes.input}
                              onChange={({ target: { value } }) =>
                                onUserBalanceChange(id, value)
                              }
                            />
                          ) : (
                            balance
                          )}
                        </TableCell>
                        <TableCell>
                          {balanceConfirmStatus ===
                            balanceConfirmStatusMap.pending && (
                            <PaperButton
                              className={classes.button}
                              onClick={() =>
                                onUserBalanceConfirm(email, balance)
                              }
                            >
                              Підтвердити баланс
                            </PaperButton>
                          )}
                        </TableCell>
                      </TableRow>
                    ),
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </TabPanel>
          <TabPanel value={value} index={1}>
            <TableContainer>
              <Table className={classes.table} aria-label='simple table'>
                <TableHead>
                  <TableRow>
                    <TableCell>Час</TableCell>
                    <TableCell>Вендор</TableCell>
                    <TableCell>Статус</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {vendors.map(({ id, name, time, status }) => (
                    <TableRow key={id} className={classes.tableRow}>
                      <TableCell
                        component='th'
                        scope='row'
                        className={classes.tabCellName}
                      >
                        {time}
                      </TableCell>
                      <TableCell onClick={() => setActiveUser(id)}>
                        {name}
                      </TableCell>
                      <TableCell>
                        <StatusCard success={status} />
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </TabPanel>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  user: state.user,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      openModal,
    },
    dispatch,
  ),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(AdminPage));
