import { makeStyles } from '@material-ui/core/styles';

import colors from '../../../../themes';
import { lighten } from '../../../../themes/utility';

export default makeStyles((theme) => ({
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    boxSizing: 'border-box',
    margin: '60px 17px 0 0',
    padding: '15px',
    height: '100%',
    width: '224px',
    background: colors.white,
    border: `1px solid ${colors.primaryLight}`,
    boxShadow: '0px 4px 10px rgba(143, 192, 222, 0.16)',
    borderRadius: '8px',
  },
  button: {
    backgroundColor: colors.greenLight,
    padding: '10px 20px',
    fontSize: '14px',
    boxShadow: '3px 3px 10px rgba(55, 194, 139, 0.3)',
    '&:hover': {
      backgroundColor: lighten(colors.greenLight, 0.1),
    },
  },
  titleBlock: {
    display: 'flex',
    justifyContent: 'center',
    borderBottom: `1px solid ${colors.grayLight}`,
    paddingBottom: '9px',
  },
  icon: {
    marginRight: '12px',
  },
  title: {
    fontSize: '20px',
    color: colors.black,
  },
  orders: {
    padding: '30px 0',
  },
  orderWrapper: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-end',
    marginBottom: '25px',
    fontSize: 14,
    '&:last-child': {
      marginBottom: '0px'
    }
  },
  orderName: {
    lineHeight: '17px',
    color: colors.black,
  },
  orderCount: {
    marginLeft: '15px',
    color: colors.secondary,
  },
  bottomCart: {
    borderTop: `1px dashed ${colors.primaryLight}`,
  },
  totalPriceWrapper: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '30px 0',
    fontWeight: 'bold',
    color: colors.black,
  },
  totalPriceTitle: {
    fontSize: '14px',
  },
  totalPriceCount: {
    fontSize: '22px',
  }
}));
