import { call, put, takeEvery } from 'redux-saga/effects';
import {
  GET_PRODUCTS_BY_VENDOR,
  getProductsLstSuccess,
  getProductsListFail,
  openModal,
} from '../store/actions';
import { getProductsByVendor } from '../api/products';

export function* fetchData(action) {
  const { vendorId } = action;

  try {
    const { products } = yield call(getProductsByVendor, vendorId);

    yield put(getProductsLstSuccess(products));
  } catch (error) {
    yield put(openModal('Error', JSON.stringify(error)));
    yield put(getProductsListFail(error));
  }
}

export function* productsSaga() {
  yield takeEvery(GET_PRODUCTS_BY_VENDOR, fetchData);
}
