import React, { useState } from 'react';
import { Route, Switch, withRouter, Redirect, NavLink } from 'react-router-dom';
import { useDispatch, connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import PaperSpinner from '../../common/components/Spinner';
import { text } from '../../config/text';
import { register, Login } from '../../store/actions';

import Divider from '../../common/components/Divider';

import RegisterForm from './components/Register';
import LoginForm from './components/Login';

import { LOGIN } from '../../store/actions';
import { getTokenFromStorage } from '../../utils/helpers';

import useStyles from './styles';

function Auth({ history, register, Login, user, userDataLoading }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const token = getTokenFromStorage();

  const [showRegisterForm, setShowRegisterForm] = useState(false);

  const onForgotPassword = () => {};

  // const routes = (
  //   <Switch>
  //     <Route
  //       exact
  //       path='/auth/login'
  //       component={() => (
  //         <LoginForm onSubmit={Login} onForgotPassword={onForgotPassword} />
  //       )}
  //     />
  //     <Route
  //       exact
  //       path='/auth/register'
  //       component={() => <RegisterForm onSubmit={register} />}
  //     />
  //     <Redirect to='/auth/login' />
  //   </Switch>
  // );

  if (userDataLoading) return <PaperSpinner />;

  if (user) {
    return <Redirect to='/vendors' />;
  }

  return (
    <div className={classes.wrapper}>
      <div className={classes.page}>
        <div className={classes.formContainer}>
          <span className={classes.formTitle}>
            {showRegisterForm
              ? text.logIn.registerTitle
              : text.logIn.logInTitle}
          </span>
          <Divider />
          <div className={classes.formHelperBlock}>
            <span className={classes.formHelperText}>
              {showRegisterForm
                ? text.logIn.alreadyRegistered
                : text.logIn.notRegistered}
            </span>
            <span
              className={classes.formHelperLink}
              onClick={() => setShowRegisterForm(!showRegisterForm)}
            >
              {showRegisterForm
                ? text.logIn.logInTitle
                : text.logIn.registerTitle}
            </span>
          </div>
          {showRegisterForm ? (
            <RegisterForm onSubmit={register} />
          ) : (
            <LoginForm onSubmit={Login} onForgotPassword={onForgotPassword} />
          )}
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  user: state.user.userData,
  userDataLoading: state.user.loading,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      register,
      Login,
    },
    dispatch,
  ),
});

// export default withRouter(Auth);
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Auth));
