import api from '../utils/api';
import { apiHost } from '../config/api';

export const getAllUsers = (token) =>
  api.get(`${apiHost}/user/all`, undefined, token);

export const addBalanceToUser = (email, balance, token) =>
  api.post(`${apiHost}/user/balance`, { email, balance }, undefined, token);

export const getTodayOrders = (token) =>
  api.get(`${apiHost}/order/today`, undefined, token);

export const setStatusToUser = (token, email, role) =>
  api.post(`${apiHost}/user/`, {email, role}, undefined, token);
