import React from 'react';
import Button from '@material-ui/core/Button';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Grow from '@material-ui/core/Grow';
import Paper from '@material-ui/core/Paper';
import Popper from '@material-ui/core/Popper';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import Avatar from '@material-ui/core/Avatar';
import { SvgIcon } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import { ReactComponent as SelectorTopIcon } from '../../../../img/svg/icons/selector_top.svg';
import { ReactComponent as SelectorBottomIcon } from '../../../../img/svg/icons/selector_bottom.svg';
import { ReactComponent as LogoutIcon } from '../../../../img/svg/icons/logout.svg';
import { header } from '../../../../config/text';
import colors from '../../../../themes';

const useStyles = makeStyles((theme) => ({
  wrapper: {
    display: 'flex',
  },
  button: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    background: colors.grayLight,
    borderTopLeftRadius: 18,
    borderBottomLeftRadius: 18,
    width: 78,
  },
  avatar: {
    height: 27,
    width: 27,
    fontSize: 16,
    background: colors.primary,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  iconWrapper: {
    height: 15,
    width: 15,
    marginTop: 5,
    marginRight: 3,
  },
  menuList: {
    marginTop: 10,
    padding: 0,
  },
  menuItem: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    '& span': {
      marginRight: 15,
    },
  },
}));

const UserMenu = ({ userLogOut, userEmail }) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef(null);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  return (
    <div className={classes.wrapper}>
      <Button
        className={classes.button}
        ref={anchorRef}
        aria-controls={open ? 'menu-list-grow' : undefined}
        aria-haspopup='true'
        onClick={handleToggle}
        disableRipple
      >
        <Avatar className={classes.avatar}>
          <span>{userEmail.substr(0, 1).toUpperCase()}</span>
        </Avatar>
        <div className={classes.iconWrapper}>
          <SvgIcon component={open ? SelectorTopIcon : SelectorBottomIcon} />
        </div>
      </Button>
      <Popper
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin:
                placement === 'bottom' ? 'center top' : 'center bottom',
            }}
          >
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList
                  autoFocusItem={open}
                  id='menu-list-grow'
                  onKeyDown={handleListKeyDown}
                  className={classes.menuList}
                >
                  <MenuItem onClick={userLogOut} className={classes.menuItem}>
                    <span>{header.logout}</span>
                    <SvgIcon component={LogoutIcon} />
                  </MenuItem>
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </div>
  );
};

export default UserMenu;
