import React, { useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import PaperInput from '../../../../common/components/Input';
import PaperButton from '../../../../common/components/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import { text } from '../../../../config/text';

import { register } from '../../../../api/Auth';

import colors from '../../../../themes';

const useStyles = makeStyles(() => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  inputContainer: {
    marginBottom: '12px',
  },
  button: {
    minWidth: 300,
    marginTop: 29,
    padding: '10px 0',
  },
}));

const RegisterForm = ({ onSubmit }) => {
  const classes = useStyles();

  const [values, setValues] = useState({
    email: '',
    showPassword: false,
    password: '',
    confirmPassword: '',
  });

  const { email, password, showPassword } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };
  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const submitForm = async (event) => {
    event.preventDefault();
    onSubmit(email, password);
  };

  const handleKeyUp = (event) => {
    if (event.keyCode === 13) return onSubmit(email, password);
  };

  return (
    <form
      className={classes.form}
      noValidate
      autoComplete='off'
      onSubmit={submitForm}
    >
      <PaperInput
        id='standard-name'
        label='Email/Tel'
        value={email}
        onChange={handleChange('email')}
        containerStyle={classes.inputContainer}
      />
      <PaperInput
        id='password'
        label='Password'
        type={showPassword ? 'text' : 'password'}
        value={password}
        onChange={handleChange('password')}
        endAdornment={
          <InputAdornment position='end'>
            <IconButton
              aria-label='toggle password visibility'
              onClick={handleClickShowPassword}
              edge='end'
            >
              {showPassword ? <Visibility /> : <VisibilityOff />}
            </IconButton>
          </InputAdornment>
        }
        containerStyle={classes.inputContainer}
      />
      <PaperInput
        id='confirm-password'
        label='Password'
        type={showPassword ? 'text' : 'password'}
        value={password}
        onChange={handleChange('password')}
        endAdornment={
          <InputAdornment position='end'>
            <IconButton
              aria-label='toggle password visibility'
              onClick={handleClickShowPassword}
              edge='end'
            >
              {showPassword ? <Visibility /> : <VisibilityOff />}
            </IconButton>
          </InputAdornment>
        }
        containerStyle={classes.inputContainer}
      />
      <PaperButton type='submit' className={classes.button}>
        {text.logIn.registerTitle}
      </PaperButton>
    </form>
  );
};

export default RegisterForm;
