import React, { Fragment, useState } from 'react';
import { withRouter } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';

import CheckoutErrorImg from '../../../img/checkoutError.png';

import PaperButton from '../../../common/components/Button';
import { cart as cartTranslations } from '../../../config/text';

import useStyles from '../styles';

function SuccessStatus({ description, onCartPress, onBack }) {
  const classes = useStyles();

  return (
    <div className={classes.wrapper}>
      <div className={classes.imageWrapper}>
        <img className={classes.image} alt='status' src={CheckoutErrorImg} />
      </div>
      <Typography className={classes.title}>
        Сталась неочікувана помилка
      </Typography>
      <Typography className={classes.description}>{description}</Typography>
      <PaperButton onClick={onCartPress} className={classes.cartButton}>
        Повернутись в кошик
      </PaperButton>
      <PaperButton onClick={onBack} className={classes.returnButton}>
        Повернутись на головну
      </PaperButton>
    </div>
  );
}

export default SuccessStatus;
