import { call, put, takeEvery } from 'redux-saga/effects';

import {
  LOGIN,
  LogedIn,
  REGISTER,
  register,
  registered,
  userLogOut,
  LOGOUT,
  GET_USER_FULL_INFO,
  recievedUserInfoFull,
  authError,
  setToCartSuccess,
  openModal,
  guestVisit,
} from '../store/actions';
import {
  callLogin,
  getFullUserInfo,
  register as registerAPI,
} from '../api/Auth';
import {
  setTokenToStorage,
  removeTokenFromStorage,
  removeCartProductsFromStorage,
  getCartProductsFromStorage,
} from '../utils/helpers';
import { setAuthToken } from '../utils/api';

function* fetchData(action) {
  const { email, password, rememberMe } = action;
  try {
    const { access_token, userInfo } = yield call(callLogin, email, password);
    console.log(access_token, userInfo, 1);

    // removeTokenFromStorage();
    setTokenToStorage(access_token);
    setAuthToken(access_token);
    yield put(LogedIn(access_token, userInfo));
    const cartOrders = getCartProductsFromStorage();
    if (cartOrders) yield put(setToCartSuccess(cartOrders));
  } catch (e) {
    removeTokenFromStorage();
    yield put(openModal('Error', JSON.stringify(e)));
    yield put(authError(e));
    console.log('Error', e);
  }
}

function* createUser(action) {
  const { email, password } = action;
  try {
    const data = yield call(registerAPI, email, password);

    yield put(registered(data));
    yield put(
      openModal(
        'Вітаємо!',
        'Ви зареєструвались!\n\nЗалишилось підтвердити ваш e-mail',
      ),
    );
  } catch (e) {
    removeTokenFromStorage();
    yield put(openModal('Error', JSON.stringify(e)));
    yield put(authError(e));
    console.log('Register error', e);
  }
}

function* logout() {
  removeTokenFromStorage();
  removeCartProductsFromStorage();
}

function* getUserInfo(action) {
  const { token } = action;

  if (!token) {
    yield put(guestVisit());
    return;
  }

  try {
    console.log('token token', token);
    // const { access_token, userInfo } = yield call(getFullUserInfo, token);
    const userInfo = yield call(getFullUserInfo, token);
    console.log('userInfo', userInfo);
    // removeTokenFromStorage();
    setTokenToStorage(token);
    setAuthToken(token);
    yield put(recievedUserInfoFull(token, userInfo));
    const cartOrders = getCartProductsFromStorage();
    if (cartOrders) yield put(setToCartSuccess(cartOrders));
  } catch (e) {
    removeTokenFromStorage();
    yield put(authError(e));
    console.log('Get user from token error', e);
  }
}

export function* userSaga() {
  yield takeEvery(LOGIN, fetchData);
  yield takeEvery(REGISTER, createUser);
  yield takeEvery(LOGOUT, logout);
  yield takeEvery(GET_USER_FULL_INFO, getUserInfo);
}
