import axios from 'axios';

import { PRODUCTION, apiHost, localApiHost } from '../config/api';

const config = {
  baseURL: apiHost,
  headers: { 'Content-Type': 'application/json' },
  transformResponse: [
    (data) => {
      return data;
    },
  ],
};

const requestInstance = axios.create(config);

let authToken = '';
export const setAuthToken = (token) => {
  requestInstance.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  authToken = `Bearer ${token}`;
};

const getParsedError = (error) => {
  try {
    const parsedError = (error || {}).data;

    if (Array.isArray(parsedError)) return parsedError.join(', ');
    if (parsedError instanceof Object)
      return Object.values(parsedError).join(', ');

    return parsedError;
  } catch (e) {
    console.log('Error is not JSON type');
  }

  return error;
};

const getParsedResponseError = (err) => {
  const error = err.response;
  const errMessage = getParsedError(error);
  const errStatus = (error || {}).status || '';
  const statusText = (error || {}).statusText || '';

  const message = `Error status ${errStatus} Message: ${errMessage}`;

  return Promise.reject(errMessage || `${errStatus}: ${statusText}`);
};

async function request({ url, method, body, params, token }) {
  const data = {
    client_id: 'testjwtclientid',
    client_secret: 'XY7kmzoNzl100',
    ...body,
  };

  try {
    const response = await requestInstance({
      headers: token ? { Authorization: `Bearer ${token}` } : undefined,
      url,
      method,
      data,
      params,
    });
    console.log('response', response);
    return JSON.parse(response.data || '{}');
  } catch (err) {
    return getParsedResponseError(err);
  }
}

async function localRequest({ url, method, body, params, token }) {
  const data = {
    client_id: 'testjwtclientid',
    client_secret: 'XY7kmzoNzl100',
    ...body,
  };

  try {
    const customURL = `${localApiHost}/request/cors?url=${url}`;
    const response = await requestInstance({
      headers: token ? { Authorization: `Bearer ${token}` } : undefined,
      url: customURL,
      method,
      data,
      params,
    });
    console.log('local response', response);
    return JSON.parse(response.data || '{}');
  } catch (err) {
    return getParsedResponseError(err);
  }
}

const apiRequest = PRODUCTION ? request : localRequest;

const api = {
  post: (url, body, params = null, token) =>
    apiRequest({ url, method: 'POST', body, params, token }),
  get: (url, params = null, token) =>
    apiRequest({ url, method: 'GET', params, token }),
};

export const getAuthToken = async (email, password) => {
  try {
    const response = await axios.post(`${localApiHost}/auth/oath2/token`, {
      client_id: 'testjwtclientid',
      client_secret: 'XY7kmzoNzl100',
      email,
      password,
    });
    console.log('response', response);
    return response.data;
  } catch (err) {
    return getParsedResponseError(err);
  }
};

export default api;
