import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import clsx from 'clsx';

import ProductItem from '../ProductItem';

import { MINIM_PRICE } from '../../../config';

import { getTotalPriceByCategory } from '../../../utils/helpers';
import { cart as cartTranslations } from '../../../config/text';
import colors from '../../../themes';

const useStyles = makeStyles((theme) => ({
  list: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    width: '100%',
    maxWidth: '926px',
    marginTop: '25px',
  },
  categoryLabelWrapper: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    marginBottom: '15px',
    textTransform: 'uppercase',
    color: colors.primary,
    fontSize: '16px',
    fontWeight: '800',
  },
  categoryLabel: {},
  categoryDesc: {
    marginLeft: '14px',
  },
  available: {
    color: colors.greenLight,
    fontSize: '14px',
  },
  notAvailable: {
    color: colors.error,
    fontSize: '14px',
  },
}));

const ProductList = ({
  category,
  products,
  showTotalCategoryPrice,
  minimumPriceRequired,
  onAddToCart,
  onRemoveFromCart,
}) => {
  const classes = useStyles();

  const totalPriceByCategory = getTotalPriceByCategory(products);
  const isMinimumAmountAchived = minimumPriceRequired
    ? getTotalPriceByCategory(products) >= MINIM_PRICE
    : true;

  return (
    <div className={classes.list}>
      {category ? (
        <div className={classes.categoryLabelWrapper}>
          <div className={classes.categoryLabel}>{category}</div>
          {showTotalCategoryPrice && (
            <div
              className={clsx(
                classes.categoryDesc,
                isMinimumAmountAchived
                  ? classes.available
                  : classes.notAvailable,
              )}
            >
              {minimumPriceRequired &&
                (isMinimumAmountAchived
                  ? cartTranslations.minimumAmountAchived
                  : `${cartTranslations.minimumAmountNotAchived} (${totalPriceByCategory}/${MINIM_PRICE})`)}
            </div>
          )}
        </div>
      ) : null}
      <>
        {products.map((product) => (
          <ProductItem
            key={product.id}
            product={product}
            isOrderAvailable={isMinimumAmountAchived}
            onAddToCart={onAddToCart}
            onRemoveFromCart={onRemoveFromCart}
          />
        ))}
      </>
    </div>
  );
};

export default ProductList;
