import React, { Component } from 'react';
import { Redirect, useHistory } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import { bindActionCreators } from 'redux';
import { getTimeSlots, getVendorsList } from '../../store/actions';
import { connect } from 'react-redux';
import { getTokenFromStorage } from '../../utils/helpers';
import Vendor from './components/Vendor';
import { vendorText } from '../../config/text';
import { getDeliveryTimeOptions } from '../../utils/helpers';
import PaperSpinner from '../../common/components/Spinner';

const useStyles = (theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    backgroundColor: '#EAEFF2',
    padding: '96px 47px 118px',
    minHeight: 'calc(100vh - 222px)',
    boxSizing: 'border-box',
    [theme.breakpoints.down('md')]: {
      padding: '40px 27px 0',
    },
    [theme.breakpoints.down('xs')]: {
      padding: '16px 8px 48px',
    },
  },
  rootTitle: {
    fontStyle: 'normal',
    fontWeight: '900',
    fontSize: '28px',
    lineHeight: '34px',
    textAlign: 'center',
    letterSpacing: '0.24px',
    color: '#121212',
    margin: '12px 0 23px',
  },
  rootText: {
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '14px',
    textAlign: 'center',
    letterSpacing: '0.14px',
    color: '#7796B1',
    margin: '0 0 55px 0',
    maxWidth: '345px',
  },
  vendorsWrapper: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    margin: '0 auto',
    maxWidth: '1200px',
  },
  vendor: {
    margin: '12px',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-end',
    justifyContent: 'flex-end',
    borderRadius: '16px',
    border: '1px solid #ffffff',
    cursor: 'pointer',
    width: '100%',
    maxWidth: '373px',
    height: '250px',
    padding: '24px',
    boxSizing: 'border-box',
  },
  vendorTime: {
    padding: '6px 14px',
    background: '#FF820F',
    boxShadow: '5px 5px 30px rgba(27, 44, 76, 0.3)',
    borderRadius: '20px',
    fontStyle: 'normal',
    fontWeight: '600',
    fontSize: '14px',
    lineHeight: '17px',
    color: '#FFFFFF',
    alignSelf: 'flex-start',
    margin: ' 0 0 auto 0',
    width: '328px',
  },
  vendorTitle: {
    fontStyle: 'normal',
    fontWeight: 'bold',
    fontSize: '32px',
    lineHeight: '37px',
    textAlign: 'right',
    letterSpacing: '1px',
    color: '#FFFFFF',
    margin: '8px 0',
  },
  vendorDescription: {
    margin: '0',
    fontStyle: 'normal',
    fontWeight: 'bold',
    fontSize: '14px',
    lineHeight: '17px',
    textAlign: 'right',
    letterSpacing: '0.45px',
    color: '#FFFFFF',
  },
});

const Vendors = ({
  actions,
  classes,
  vendorsList,
  user,
  timeSlots,
  isLoading,
}) => {
  const history = useHistory();
  React.useEffect(() => {
    actions.getVendorsList();
    actions.getTimeSlots();
  }, []);

  if (!user) {
    return <Redirect to='/auth' />;
  }

  if (isLoading) return <PaperSpinner />;

  const navigateTo = () => history.push('/cart');

  const { label: closestDeliveryTimeOption } =
    getDeliveryTimeOptions(timeSlots).find(({ disabled }) => !disabled) || {};

  const onVendorClick = (vendorId) => {
    history.push(`/vendors/${vendorId}`);
  };

  return (
    <div className={classes.root}>
      <svg
        width='51'
        height='39'
        viewBox='0 0 51 39'
        fill='none'
        xmlns='http://www.w3.org/2000/svg'
      >
        <path
          d='M49.4062 32.5H1.59375C1.16211 32.5 0.788574 32.3392 0.473145 32.0176C0.157715 31.696 0 31.3151 0 30.875C0 24.5104 1.93408 19.0853 5.80225 14.5996C9.67041 10.1139 14.6426 7.41406 20.7188 6.5C21.1504 6.5 21.5239 6.33919 21.8394 6.01758C22.1548 5.69596 22.3125 5.3151 22.3125 4.875C22.3125 4.4349 22.1548 4.05404 21.8394 3.73242C21.5239 3.41081 21.1504 3.25 20.7188 3.25C20.2871 3.25 19.9136 3.08919 19.5981 2.76758C19.2827 2.44596 19.125 2.0651 19.125 1.625C19.125 1.1849 19.2827 0.804036 19.5981 0.482422C19.9136 0.160807 20.2871 0 20.7188 0H30.2812C30.7129 0 31.0864 0.160807 31.4019 0.482422C31.7173 0.804036 31.875 1.1849 31.875 1.625C31.875 2.0651 31.7173 2.44596 31.4019 2.76758C31.0864 3.08919 30.7129 3.25 30.2812 3.25C29.8496 3.25 29.4761 3.41081 29.1606 3.73242C28.8452 4.05404 28.6875 4.4349 28.6875 4.875C28.6875 5.3151 28.8452 5.69596 29.1606 6.01758C29.4761 6.33919 29.8496 6.5 30.2812 6.5C36.3574 7.41406 41.3296 10.1139 45.1978 14.5996C49.0659 19.0853 51 24.5104 51 30.875C51 31.3151 50.8423 31.696 50.5269 32.0176C50.2114 32.3392 49.8379 32.5 49.4062 32.5ZM1.59375 35.75H49.4062C49.8379 35.75 50.2114 35.9108 50.5269 36.2324C50.8423 36.554 51 36.9349 51 37.375C51 37.8151 50.8423 38.196 50.5269 38.5176C50.2114 38.8392 49.8379 39 49.4062 39H1.59375C1.16211 39 0.788574 38.8392 0.473145 38.5176C0.157715 38.196 0 37.8151 0 37.375C0 36.9349 0.157715 36.554 0.473145 36.2324C0.788574 35.9108 1.16211 35.75 1.59375 35.75Z'
          fill='#8FC0DE'
        />
      </svg>
      <h1 className={classes.rootTitle}>{vendorText.title}</h1>
      <h3 className={classes.rootText}>{vendorText.text}</h3>
      <div className={classes.vendorsWrapper}>
        {(vendorsList || []).map((vendor) =>
          Vendor(vendor, closestDeliveryTimeOption, classes, onVendorClick),
        )}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  user: state.user.userData,
  vendorsList: state.vendors.list,
  timeSlots: state.timeSlots.list,
  isLoading: state.vendors.loading,
});

const mapDispatchToProps = (dispatch) => ({
  actions: bindActionCreators(
    {
      getVendorsList,
      getTimeSlots,
    },
    dispatch,
  ),
});

export default withStyles(useStyles)(
  connect(mapStateToProps, mapDispatchToProps)(Vendors),
);
