import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

import colors from '../../../../themes';
import { cart as cartTranslations } from '../../../../config/text';

import useStyles from './styles';

const GreenRadio = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: colors.greenLight,
    },
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
  checked: {},
})((props) => <Radio color='default' {...props} />);

export default function DeliveryOptions({
  deliveryTimeOptions,
  onChooseDeliveryTime,
  value,
}) {
  const classes = useStyles();
  console.log('deliveryTimeOptions');
  console.log('deliveryTimeOptions dedeed', value);
  return (
    <div className={classes.wrapper}>
      <span className={classes.timeSlotsTitle}>
        {cartTranslations.chooseDeliveryTime}
      </span>
      <RadioGroup
        aria-label='time-slots'
        name='time-slots'
        value={value}
        onChange={onChooseDeliveryTime}
        className={classes.timeSlotBar}
      >
        {deliveryTimeOptions.map(({ label, value, disabled }) => (
          <FormControlLabel
            key={value}
            value={value}
            control={<GreenRadio />}
            label={label}
            disabled={disabled}
          />
        ))}
      </RadioGroup>
    </div>
  );
}
