import { makeStyles } from '@material-ui/core/styles';
import colors from '../../themes';
import LoginBackground from '../../img/dots_background.png';

export default makeStyles((theme) => ({
  wrapper: {
    width: '100%',
    height: 'calc(100vh - 222px)',
    background: `linear-gradient(180deg, ${colors.primaryLight} -20.55%, rgba(234, 239, 242, 0) 74.75%)`,
  },
  page: {
    width: '100%',
    height: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundImage: `url(${LoginBackground})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'top right',
  },
  formContainer: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    padding: '4% 9%',
    maxHeight: '655px',
    maxWidth: '644px',
    backgroundColor: colors.white,
    boxShadow: '3px 3px 20px rgba(143, 192, 222, 0.4)',
  },
  formTitle: {
    fontSize: '32px',
    textTransform: 'uppercase',
    color: colors.primary,
    fontWeight: 'bold',
  },
  formHelperBlock: {
    fontSize: '14px',
    color: colors.primary,
    marginBottom: '40px',
  },
  formHelperText: {
    marginRight: '5px',
  },
  formHelperLink: {
    textDecoration: 'underline',
    fontWeight: 'bold',
    cursor: 'pointer',
  },
  inputContainer: {
    marginBottom: '24px',
  },
  button: {
    minWidth: 300,
    marginTop: 19,
    padding: '10px 0',
  },
}));
