import { makeStyles } from '@material-ui/core/styles';

import colors from '../../../../themes';

export default makeStyles((theme) => ({
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
  },
  timeSlotsTitle: {
    fontWeight: '800',
    fontSize: '16px',
    lineHeight: '19px',
    textTransform: 'uppercase',
    color: colors.primary,
  },
  timeSlotBar: {
    display: 'flex',
    flexDirection: 'row',
    marginLeft: '50px',
    marginTop: '30px',
  },
}));
