import React from 'react';
import clsx from 'clsx';

import Typography from '@material-ui/core/Typography';

import NoProductImg from '../../../img/no-product-photo.png';
import PaperButton from '../Button';
import PaperExpanderButton from '../Expander';

import createStyles from './styles';

const BucketButton = ({ amount, onPlusClick, onMinusClick }) => {
  const classes = createStyles();

  return amount > 0 ? (
    <div className={classes.amountBtnContainer}>
      <button className={classes.amountBtnMinus} onClick={onMinusClick}>
        -
      </button>
      <div className={classes.amountBtnCount}>{amount}</div>
      <button className={classes.amountBtnPlus} onClick={onPlusClick}>
        +
      </button>
    </div>
  ) : (
    <PaperButton onClick={onPlusClick} className={classes.bucketButton}>
      В кошик
    </PaperButton>
  );
};

export default BucketButton;
