import React, { Fragment, useState } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';

import SuccessStatus from './components/Success';
import ErrorStatus from './components/Error';

import CheckoutSuccessImg from '../../img/checkoutSuccess.png';
import CheckoutErrorImg from '../../img/checkoutError.png';
import ClockImg from '../../img/clock.png';

import PaperButton from '../../common/components/Button';
import { cart as cartTranslations } from '../../config/text';

import useStyles from './styles';

function CheckoutStatus({
  history,
  location: {
    state: { status },
  },
  cartError,
}) {
  const classes = useStyles();
  const onBackToMain = () => history.push('/vendors');
  const onBackToCart = () => history.push('/cart');

  return (
    <div className={classes.page}>
      {status === 'success' ? (
        <SuccessStatus onBack={onBackToMain} />
      ) : (
        <ErrorStatus
          onCartPress={onBackToCart}
          onBack={onBackToMain}
          description={cartError}
        />
      )}
    </div>
  );
}

const mapStateToProps = (state) => ({
  cartError: state.cart.error,
});

export default connect(mapStateToProps, null)(withRouter(CheckoutStatus));
