import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Checkbox from '@material-ui/core/Checkbox';
import clsx from 'clsx';

const useStyles = makeStyles(() => ({
  container: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
}));

const getThemedCheckbox = (checkboxColor) =>
  withStyles({
    root: {
      color: checkboxColor,
      padding: 0,
      marginLeft: '-2px',

      '&$checked': {
        color: checkboxColor,
      },
      '&:hover': {
        backgroundColor: 'transparent',
      },
    },
    checked: {},
  })((props) => <Checkbox color='red' {...props} />);

export default function PaperCheckbox({
  name,
  label,
  onChange,
  containerClassName,
  labelClassName,
  checkboxColor,
}) {
  const [checked, setChecked] = React.useState(false);
  const classes = useStyles();

  const handleChange = (event) => {
    setChecked(event.target.checked);
    if (onChange) onChange(event.target.checked);
  };

  const ThemedCheckbox = getThemedCheckbox(checkboxColor);

  return (
    <div className={clsx(classes.container, containerClassName)}>
      <ThemedCheckbox
        checked={checked}
        onChange={handleChange}
        name={name}
        disableFocusRipple
        disableTouchRipple
      />
      <div className={labelClassName}>{label}</div>
    </div>
  );
}
