import { call, all, put, takeEvery, select } from 'redux-saga/effects';
import {
  SET_TO_CART_REQUEST,
  SET_TO_CART_SUCCESS,
  REMOVE_FROM_CART_REQUEST,
  PLACE_ORDER_REQUEST,
  setToCartSuccess,
  placeOrderSuccess,
  placeOrderError,
  openModal,
  getUserFullInfo,
} from '../store/actions';
import { getAuthToken } from '../store/selectors/user';
import { getProductsFromCart } from '../store/selectors/cart';
import { getProductsByVendor } from '../api/products';
import { placeOrderRequest } from '../api/cart';
import {
  setCartProductsToStorage,
  removeCartProductsFromStorage,
} from '../utils/helpers';

function* handleSetToCart(action) {
  const { product } = action;
  const cartProducts = yield select(getProductsFromCart);

  const productInCart = cartProducts.find(({ id }) => id === product.id);
  const newCartProducts = productInCart
    ? cartProducts.map((item) => {
        return item.id === product.id
          ? {
              ...item,
              count: item.count + 1,
            }
          : item;
      })
    : [...cartProducts, { ...product, count: 1 }];

  yield put(setToCartSuccess(newCartProducts));
}

function* handleRemoveFromCart(action) {
  const { product } = action;
  const cartProducts = yield select(getProductsFromCart);

  const newCartProducts = cartProducts
    .map((item) => {
      return item.id === product.id
        ? {
            ...item,
            count: item.count - 1,
          }
        : item;
    })
    .filter(({ count }) => count > 0);

  yield put(setToCartSuccess(newCartProducts));
}

function* handlePlaceOrder(action) {
  const { timeSlot, callback } = action;
  const cartProducts = yield select(getProductsFromCart);

  const orderData = Object.values(
    cartProducts.reduce(
      (acc, { vendorId, id: productId, count }) => ({
        ...acc,
        [vendorId]: acc[vendorId]
          ? {
              ...acc[vendorId],
              products: { ...acc[vendorId].products, [productId]: count },
            }
          : {
              vendorId,
              timeSlot: { id: timeSlot },
              products: { [productId]: count },
            },
      }),
      {},
    ),
  );

  console.log('orderData', orderData);
  try {
    yield all(orderData.map((order) => call(placeOrderRequest, order)));

    yield put(setToCartSuccess([]));

    const userToken = yield select(getAuthToken);
    const userInfo = yield put(getUserFullInfo(userToken));

    removeCartProductsFromStorage();
    callback(true);
  } catch (error) {
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log('error');
    console.log(error);
    // yield put(openModal('Error', JSON.stringify(error)));
    yield put(placeOrderError(error));
    callback(false);
  }
}

function handleSetToCartSuccess(action) {
  const { data } = action;
  setCartProductsToStorage(data);
}

export function* cartSaga() {
  yield takeEvery(SET_TO_CART_REQUEST, handleSetToCart);
  yield takeEvery(REMOVE_FROM_CART_REQUEST, handleRemoveFromCart);
  yield takeEvery(SET_TO_CART_SUCCESS, handleSetToCartSuccess);
  yield takeEvery(PLACE_ORDER_REQUEST, handlePlaceOrder);
}
