import { makeStyles } from '@material-ui/core/styles';

import { lighten } from '../../themes/utility';
import colors from '../../themes';

export default makeStyles((theme) => ({
  page: {
    minHeight: 'calc(100vh - 222px)',
    background: colors.grayLight,
    boxSizing: 'border-box',
    padding: '40px 0',
  },
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
    marginTop: '30px',
  },
  tabTitle: {
    fontWeight: '900',
    fontSize: '28px',
    color: colors.black,
  },
  tabs: {
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
  },
  tab: {},
  tabIndicator: {
    backgroundColor: colors.greenLight,
  },
  button: {
    backgroundColor: colors.greenLight,
    padding: '10px 20px',
    fontSize: '14px',
    boxShadow: '3px 3px 10px rgba(55, 194, 139, 0.3)',
    '&:hover': {
      backgroundColor: lighten(colors.greenLight, 0.1),
    },
  },
  confirmUserButton: {
    backgroundColor: colors.secondary,
    padding: '10px 20px',
    fontSize: '14px',
  },
  tableRow: {
    '&:nth-child(odd)': { background: colors.white },
  },
  tabCellName: {
    minWidth: '250px',
  },
  inputContainer: {
    width: '80px',
  },
  input: {
    padding: '8px 7px',
  },
}));
