import { makeStyles } from '@material-ui/core/styles';

import colors from '../../themes';
import { lighten } from '../../themes/utility';

export const useStyles = makeStyles((theme) => ({
  wrapper: {
    width: '100%',
    height: 81,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    background: colors.white,
    borderBottom: `1px solid #D8EBF8`,
    boxShadow: `0px 4px 10px rgba(143, 192, 222, 0.16)`,
  },
  container: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    width: '100%',
    margin: '0 5%',
  },
  logoWrapper: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  title: {
    marginLeft: 35,
    textTransform: 'uppercase',
    fontSize: 12,
  },
  headerItems: {
    display: 'flex',
  },
  headerItem: {
    display: 'flex',
    alignItems: 'center',
    '& span': {
      fontSize: 14,
      fontWeight: 'bold',
      '&:nth-child(2)': {
        marginLeft: 7,
      },
    },
    marginRight: 33,
    '&:last-child': {
      marginRight: 0,
    },
  },
  bucket: {
    '&:hover': {
      opacity: 0.8,
      cursor: 'pointer',
    },
  },
  balance: {
    color: colors.greenLight,
  },
  adminButton: {
    background: colors.orange,
    '&:hover': {
      background: lighten(colors.orange, 0.1),
    },
  },
}));
