import { makeStyles } from '@material-ui/core/styles';

import { lighten } from '../../themes/utility';
import colors from '../../themes';

export default makeStyles((theme) => ({
  page: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: 'calc(100vh - 222px)',
    background: `linear-gradient(180deg, ${colors.primaryLight} -20.55%, rgba(234, 239, 242, 0) 74.75%)`,
    boxSizing: 'border-box',
  },
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    maxWidth: '500px',
  },
  imageWrapper: {
    height: '106px',
    width: '106px',
  },
  image: {
    height: '100%',
    width: '100%',
  },
  title: {
    margin: '30px 0',
    fontStyle: 'normal',
    fontWeight: '900',
    fontSize: '28px',
    lineHeight: '34px',
    letterSpacing: '0.24px',
    color: colors.black,
    textAlign: 'center',
  },
  description: {},
  infoWrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  clockImageWrapper: {},
  clockImage: {},
  info: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    marginTop: '10px',
    fontSize: '14px',
    lineHeight: '160%',
    fontWeight: 'normal',
    color: colors.secondary,
  },
  returnButton: {
    marginTop: '55px',
    width: '300px',
    padding: '10px 25px',
  },
  cartButton: {
    marginTop: '55px',
    width: '300px',
    padding: '10px 25px',
    color: colors.primary,
    backgroundColor: colors.white,
    border: `1px solid ${colors.primaryLight}`,
    '&:hover': {
      color: colors.primaryLight,
      backgroundColor: colors.white,
    },
  },
}));
