import React from 'react';
import { useHistory } from 'react-router-dom';

import PaperButton from '../../../../common/components/Button';
import { ReactComponent as BucketIcon } from '../../../../img/svg/icons/bucket.svg';

import { getCartTotalPrice } from '../../../../utils/helpers';

import useStyles from './styles';

function Cart({ orders = [] }) {
  const classes = useStyles();
  const history = useHistory();
  const navigateTo = () => history.push('/cart'); //eg.history.push('/login');

  const cartTotalPrice = getCartTotalPrice(orders);

  return (
    <div className={classes.wrapper}>
      <div className={classes.titleBlock}>
        <BucketIcon className={classes.icon} />
        <span className={classes.title}>Ваш кошик</span>
      </div>
      <div className={classes.orders}>
        {(
          orders || [
            { name: 'Суп з зеленого горошку', count: 3 },
            { name: 'Суп з зеленого горошку and bacon', count: 3 },
          ]
        ).map(({ name, count }) => (
          <div className={classes.orderWrapper}>
            <div className={classes.orderName}>{name}</div>
            <div className={classes.orderCount}>{count}</div>
          </div>
        ))}
      </div>
      <div className={classes.bottomCart}>
        <div className={classes.totalPriceWrapper}>
          <div className={classes.totalPriceTitle}>Загальна сума</div>
          <div className={classes.totalPriceCount}>{cartTotalPrice}₴</div>
        </div>
        <PaperButton
          className={classes.button}
          onClick={navigateTo}
          disabled={!cartTotalPrice}
        >
          Перейти в кошик
        </PaperButton>
      </div>
    </div>
  );
}

export default Cart;
