import {
  LOGEDIN,
  REGISTER,
  REGISTERED,
  GET_USER_FULL_INFO,
  USER_INFO_FULL_RECEIVED,
  GUEST_VISIT,
  LOGIN,
  LOGOUT,
  AUTH_ERROR,
} from '../actions';

const initialState = {
  userData: null,
  token: null,
  loading: true,
};

export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case LOGIN:
    case GET_USER_FULL_INFO:
    case REGISTER:
      return { ...state, loading: true };
    case GUEST_VISIT:
    case LOGOUT:
    case REGISTERED:
      return { ...state, loading: false };
    case LOGEDIN:
      return {
        ...state,
        userData: action.userData,
        token: action.token,
        loading: false,
        error: null,
      };
    case USER_INFO_FULL_RECEIVED:
      return {
        ...state,
        userData: action.userData,
        token: action.token,
        loading: false,
        error: null,
      };
    case AUTH_ERROR:
      return {
        ...state,
        userData: null,
        token: null,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
}
