import {call, put, takeEvery} from 'redux-saga/effects';
import {GET_TIME_SLOTS, getTimeSlotsSuccess} from '../store/actions';
import {getTimeSlotsAPI} from '../api/TimeSlots';

export function* fetchData(action) {
  try {
    const {timeSlots} = yield call(getTimeSlotsAPI);
    yield put(getTimeSlotsSuccess(timeSlots));
  } catch (e) {
    console.log('TIME SLOTS ERR', e);
  }
}

export function* timeSlotsSage() {
  yield takeEvery(GET_TIME_SLOTS, fetchData);
}
