import { makeStyles } from '@material-ui/core/styles';

import colors from '../../themes';

export default makeStyles((theme) => ({
  page: {
    background: colors.grayLight,
    boxSizing: 'border-box',
    padding: '40px 0',
  },
  infoBar: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    textAlign: 'center',
    paddingTop: '19px',
  },
  title: {
    fontStyle: 'normal',
    fontWeight: '900',
    fontSize: '28px',
    lineHeight: '34px',
    letterSpacing: '0.24px',
    color: colors.black,
  },
  wrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
    marginTop: '30px',
  },
  productsWrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: '30px',
  },
  checkoutWrapper: {
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'center',
    marginTop: '40px',
  },
  checkoutInfo: {
    fontWeight: '600',
    fontSize: '30px',
    color: colors.primary,
    '& span:last-child': {
      marginLeft: '28px',
    },
  },
  checkoutButton: {
    marginLeft: '36px',
  },
}));
