import React from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputLabel from '@material-ui/core/InputLabel';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));
export default function AddBalance() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    user: ''
  });

  const inputLabel = React.useRef(null);
  const [labelWidth, setLabelWidth] = React.useState(0);
  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, []);

  const handleChange = name => event => {
    setState({
      ...state,
      [name]: event.target.value,
    });
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                name="amount"
                variant="outlined"
                required
                fullWidth
                id="amount"
                label="Сума"
                type="number"
              />
            </Grid>
            <Grid item xs={12}>
            <FormControl variant="outlined" className={classes.formControl} fullWidth>
        <InputLabel ref={inputLabel} htmlFor="select-user">
          Користувач
        </InputLabel>
        <Select
          native
          value={state.user}
          onChange={handleChange('user')}
          input={
            <OutlinedInput name="user" labelWidth={labelWidth} id="select-user" />
          }
        >
          <option value="" />
          <option value={"0"}>Користувач 1</option>
          <option value={"1"}>Користувач 2</option>
          <option value={"2"}>Користувач 3</option>
        </Select>
      </FormControl>
      </Grid>
            <Grid item xs={12}>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="secondary"
            className={classes.submit}
          >
            Поповнити баланс
          </Button>
          </Grid>
          </Grid>
      
        </form>
      </div>
      <Box mt={5}>
      </Box>
    </Container>
  );
}