import React, { Fragment, useState } from 'react';
import { withRouter } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';

import CheckoutSuccessImg from '../../../img/checkoutSuccess.png';
import CheckoutErrorImg from '../../../img/checkoutError.png';
import ClockImg from '../../../img/clock.png';

import PaperButton from '../../../common/components/Button';
import { cart as cartTranslations } from '../../../config/text';

import useStyles from '../styles';

function SuccessStatus({ onBack }) {
  const classes = useStyles();

  return (
    <div className={classes.wrapper}>
      <div className={classes.imageWrapper}>
        <img className={classes.image} alt='status' src={CheckoutSuccessImg} />
      </div>
      <Typography className={classes.title}>
        Ваше замовлення успішно оформлено!
      </Typography>
      <div className={classes.infoWrapper}>
        <div className={classes.clockImageWrapper}>
          <img className={classes.clockImage} alt='status' src={ClockImg} />
        </div>
        <div className={classes.info}>
          <span>Очікуйте на доставку.</span>
          <span>Смачного!</span>
        </div>
      </div>
      <PaperButton onClick={onBack} className={classes.returnButton}>
        Повернутись на головну
      </PaperButton>
    </div>
  );
}

export default SuccessStatus;
