import moment from 'moment';

import {DEFAULT_DELIVERY_OPTIONS} from '../config';

export const setTokenToStorage = (token) =>
  localStorage.setItem('token', token);

export const getTokenFromStorage = () => localStorage.getItem('token');

export const removeTokenFromStorage = () => localStorage.removeItem('token');

export const setCartProductsToStorage = (data) => {
  localStorage.removeItem('cartProducts');
  localStorage.setItem('cartProducts', JSON.stringify(data));
};

export const getCartProductsFromStorage = () => {
  const cartProducts = localStorage.getItem('cartProducts');
  return cartProducts ? JSON.parse(cartProducts) : null;
};

export const removeCartProductsFromStorage = (data) =>
  localStorage.removeItem('cartProducts');

export function dataToQueryParams(data) {
  if (typeof data !== 'object' || Array.isArray(data) || data === null) {
    return '';
  }

  return Object.keys(data)
    .map((key) => `${key}=${data[key]}`)
    .join('&');
}

export const toUrlEncoded = (obj) =>
  Object.keys(obj)
    .map((k) => `${encodeURIComponent(k)}=${encodeURIComponent(obj[k])}`)
    .join('&');

export const getTotalPriceByCategory = (products) =>
  products.reduce((acc, {price, count = 1}) => acc + Number(price) * count, 0);

export const getDeliveryTimeOptions = (
  deliveryTimeSlots = DEFAULT_DELIVERY_OPTIONS,
) => {
  console.log('deliveryTimeSlots', deliveryTimeSlots);
  const [currentHour, currentMinute] = moment().format('HH:MM').split(':');
  const currentTimeMoment = moment({
    h: parseInt(currentHour),
    m: parseInt(currentMinute),
  });

  const isTimeOptionAvailable = (option) => {
    const [optionHour, optionMinute] = option.split(':');
    const optionTimeMoment = moment({
      h: parseInt(optionHour),
      m: parseInt(optionMinute),
    });

    return optionTimeMoment.diff(currentTimeMoment, 'minutes') > 5;
  };

  return deliveryTimeSlots.map(({id, time}) => ({
    value: id,
    label: time,
    disabled: !isTimeOptionAvailable(time),
  }));
};

export const getCartTotalPrice = (cartProducts) =>
  cartProducts.reduce((acc, {price, count}) => acc + price * count, 0);

export const getProductsByCategory = (products) =>
  products.reduce((acc, {vendorName, ...val}) => {
    acc[vendorName] = acc[vendorName] ? [...acc[vendorName], val] : [val];
    return acc;
  }, {});
