import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';

import colors from '../../../themes';

function PaperSpinner() {
  return (
    <CircularProgress
      size={70}
      style={{
        position: 'absolute',
        top: '40%',
        left: '50%',
        color: colors.third,
      }}
    />
  );
}

export default PaperSpinner;
