import React from 'react';
import clsx from 'clsx';
import { withRouter } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { SvgIcon } from '@material-ui/core';

import { ReactComponent as SelectorLeftIcon } from '../../../img/svg/icons/selector_left.svg';

import colors from '../../../themes';

const useStyles = makeStyles((theme) => ({
  wrapper: {
    display: 'flex',
    justifyContent: 'flex-start',
    alignItems: 'center',
    padding: '0 5%',
    fontSize: '14px',
    cursor: 'pointer',
  },
  iconWrapper: {
    height: '14px',
    width: '10px',
  },
  icon: {},
  label: {
    marginLeft: '5px',
    fontWeight: '700',
    color: colors.secondary,
  },
}));

const PaperBackButton = ({ containerClassName, history: { goBack } = {} }) => {
  const classes = useStyles();

  return (
    <div className={clsx(classes.wrapper, containerClassName)} onClick={goBack}>
      <div className={classes.iconWrapper}>
        <SvgIcon className={classes.icon} component={SelectorLeftIcon} />
      </div>
      <div className={classes.label}>Назад</div>
    </div>
  );
};

export default withRouter(PaperBackButton);
