import { makeStyles } from '@material-ui/core/styles';

import colors from '../../themes';

export default makeStyles((theme) => ({
  page: {
    minHeight: 'calc(100vh - 222px)',
    background: colors.grayLight,
    boxSizing: 'border-box',
    padding: '40px 0',
  },
  // paper: {
  //   padding: theme.spacing(2),
  //   margin: 'auto',
  //   maxWidth: 500,
  // },
  // image: {
  // },
  // img: {
  //   margin: 'auto',
  //   display: 'block',
  //   maxWidth: '100%',
  //   maxHeight: '100%',
  // },
  infoBar: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    textAlign: 'center',
    paddingTop: '19px',
  },
  title: {
    fontStyle: 'normal',
    fontWeight: '900',
    fontSize: '28px',
    lineHeight: '34px',
    letterSpacing: '0.24px',
    color: colors.black,
  },
  timeSlotBarWrapper: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '16px',
    width: '100%',
  },
  timeSlotBar: {
    display: 'flex',
    alignItems: 'center',
    border: `1px solid ${colors.primaryLight}`,
    background: colors.white,
    boxSizing: 'border-box',
    borderRadius: '20px',
    width: 'fit-content',
    color: colors.primary,
    fontSize: '14px',
    padding: '6px 14px',
  },
  timeSlotText: {},
  timeSlotUnavailable: {
    color: colors.error,
  },
  timeSlotValue: {
    marginLeft: '5px',
    fontWeight: '600',
  },
  content: {
    display: 'flex',
    justifyContent: 'center',
  },
  productsWrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: '35px',
  },
  productsUnavailableWrapper: {
    minHeight: 'calc(100vh - 320px)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  productsUnavailableTitle: {},
}));
