import React, { Fragment, useState } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter, Redirect } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';

import NoProductImg from '../img/no-product-photo.png';
import PlusButton from '../img/product view/plus button bg.png';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    maxWidth: 700,
    height: 100,
  },
  dishImage: {
    width: 80,
    height: 80,
  },
  plusImage: {
    width: 42,
    height: 42,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
}));

function ProductView(tile) {
  const classes = useStyles();

  return (
    <div className={classes.page}>
      {
        <Paper className={classes.paper} style={{ margin: 5, padding: 5 }}>
          <Grid container xs={12} spacing={2}>
            <Grid item xs={4}>
              <Paper
                square={false}
                className={classes.dishImage}
                style={{ margin: 10 }}
              >
                <img
                  className={classes.img}
                  alt='dish'
                  src={tile.img ? tile.img : NoProductImg}
                />
              </Paper>
            </Grid>

            <Grid item xs={6} sm container direction='column'>
              <Grid item xs>
                <Typography gutterBottom variant='subtitle1'>
                  {tile.name}
                </Typography>
                <Typography noWrap='true' variant='subtitle2' gutterBottom>
                  {tile.desc}
                </Typography>
              </Grid>
              <Grid item sm container direction='row'>
                <Grid item xs={5}>
                  <Typography gutterBottom variant='body2'>
                    {tile.weight}г
                  </Typography>
                </Grid>
                <Grid item xs={5}>
                  <Typography gutterBottom variant='body2'>
                    {tile.price}₴
                  </Typography>
                </Grid>
              </Grid>
            </Grid>

            <Grid item xs={2}>
              <Box
                className={classes.plusImage}
                alignItems='center'
                justify='center'
              >
                <img className={classes.img} alt='plus' src={PlusButton} />
              </Box>
            </Grid>
          </Grid>
        </Paper>
      }
    </div>
  );
}

export default withRouter(ProductView);
