import React, { Fragment, useState } from 'react';
import { withRouter, useHistory } from 'react-router-dom';
import moment from 'moment';
import Typography from '@material-ui/core/Typography';

import productsData from '../../common/menuData';
import PaperBackButton from '../../common/components/BackButton';
import ProductList from '../../common/components/ProductList';
import PaperButton from '../../common/components/Button';
import { cart as cartTranslations } from '../../config/text';
import {
  getTotalPriceByCategory,
  getDeliveryTimeOptions,
  getProductsByCategory,
} from '../../utils/helpers';
import { MINIM_PRICE, MINIMUM_PRICE_REQUIRED } from '../../config';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import {
  getProductsList,
  setToCartRequest,
  removeFromCartRequest,
  placeOrderRequest,
} from '../../store/actions';
import { getProductsFromCart } from '../../store/selectors/cart';

import DeliveryOptionBar from './components/DeliveryOptionBar';
import useStyles from './styles';

function Cart({
  user,
  productsFromCart,
  getProductsList,
  setToCartRequest,
  removeFromCartRequest,
  placeOrderRequest,
  timeSlots,
}) {
  const classes = useStyles();
  const history = useHistory();
  const deliveryTimeOptions = getDeliveryTimeOptions(timeSlots);
  const { value: closestDeliveryTimeValue } =
    deliveryTimeOptions.find(({ disabled }) => !disabled) || {};

  const [deliveryTimeValue, setDeliveryTime] = useState(
    closestDeliveryTimeValue,
  );

  const computedProductsData = getProductsByCategory(productsFromCart);
  const categories = Object.keys(computedProductsData);

  const onChooseDeliveryTime = (event) => {
    setDeliveryTime(event.target.value);
  };

  const availableCategories = MINIMUM_PRICE_REQUIRED
    ? categories.filter(
        (category) =>
          getTotalPriceByCategory(computedProductsData[category]) >=
          MINIM_PRICE,
      )
    : categories;

  const checkoutPrice = availableCategories.reduce(
    (acc, category) =>
      acc + getTotalPriceByCategory(computedProductsData[category]),
    0,
  );

  const placeOrderCallback = (success) => {
    history.push({
      pathname: '/checkout-status',
      state: { status: success ? 'success' : 'error' },
    });
  };

  if (!categories.length) {
    history.push({
      pathname: '/vendors',
    });
  }

  return (
    <div className={classes.page}>
      <PaperBackButton />
      <div className={classes.infoBar}>
        <Typography className={classes.title}>
          {cartTranslations.title}
        </Typography>
      </div>
      <div className={classes.wrapper}>
        <div className={classes.content}>
          <DeliveryOptionBar
            onChooseDeliveryTime={onChooseDeliveryTime}
            deliveryTimeOptions={deliveryTimeOptions}
            value={deliveryTimeValue}
          />
          <div className={classes.productsWrapper}>
            {availableCategories.map((category) => (
              <ProductList
                key={category}
                category={category}
                products={computedProductsData[category]}
                onAddToCart={setToCartRequest}
                onRemoveFromCart={removeFromCartRequest}
                minimumPriceRequired={MINIMUM_PRICE_REQUIRED}
                showTotalCategoryPrice
              />
            ))}
          </div>
          <div className={classes.checkoutWrapper}>
            <div className={classes.checkoutInfo}>
              <span>Загальна сума:</span>
              <span>{checkoutPrice}₴</span>
            </div>
            <PaperButton
              onClick={() =>
                placeOrderRequest(deliveryTimeValue, placeOrderCallback)
              }
              className={classes.checkoutButton}
              disabled={!deliveryTimeValue}
            >
              Замовити
            </PaperButton>
          </div>
          {MINIMUM_PRICE_REQUIRED && (
            <div className={classes.productsWrapper}>
              {categories
                .filter(
                  (category) =>
                    getTotalPriceByCategory(computedProductsData[category]) <
                    MINIM_PRICE,
                )
                .map((category) => (
                  <ProductList
                    key={category}
                    category={category}
                    products={computedProductsData[category]}
                    onAddToCart={setToCartRequest}
                    onRemoveFromCart={removeFromCartRequest}
                    showTotalCategoryPrice
                  />
                ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  user: state.user.userData,
  productsFromCart: getProductsFromCart(state),
  timeSlots: state.timeSlots.list,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      getProductsList,
      setToCartRequest,
      removeFromCartRequest,
      placeOrderRequest,
    },
    dispatch,
  ),
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Cart));
