import React from 'react';
import clsx from 'clsx';

import Typography from '@material-ui/core/Typography';
import { SvgIcon } from '@material-ui/core';

import NoProductImg from '../../../img/no-product-photo.png';
import WarningIconImg from '../../../img/warning_icon.png';
import PaperBucketButton from '../BucketButton';
import PaperExpanderButton from '../Expander';

import createStyles from './styles';

const getDescription = (text, isExpanded) =>
  isExpanded ? text : text ? `${text.substring(0, 98)}...` : null;

const ProductItem = ({
  product,
  onAddToCart,
  onRemoveFromCart,
  isOrderAvailable,
}) => {
  const classes = createStyles();
  const {
    name,
    computedDescription: desc,
    price,
    weight,
    iconUrl: image,
    count,
  } = product;

  const [expanded, setExpanded] = React.useState(false);
  const [amount, setAmount] = React.useState(0);

  return (
    <div className={classes.item}>
      <div className={classes.itemLeftSide}>
        <div
          className={clsx(
            classes.imageWrapper,
            expanded ? classes.extendedImage : null,
          )}
        >
          <img
            className={classes.image}
            alt='dish'
            src={image ? image : NoProductImg}
          />
        </div>
        <div className={classes.itemInfo}>
          <div className={classes.itemInfoTop}>
            <div className={classes.itemName}>{name}</div>
            <div className={classes.itemDescription}>
              {getDescription(desc, expanded)}
            </div>
          </div>
          <div className={classes.itemInfoBottom}>
            <div className={classes.itemWeight}>{weight} г</div>
            <PaperExpanderButton
              label={'Розгорнути'}
              expanded={expanded}
              className={clsx(expanded ? classes.extraMarginTop : null)}
              onClick={() => setExpanded(!expanded)}
            />
          </div>
        </div>
      </div>
      <div className={classes.itemRightSide}>
        <div className={classes.itemPrice}>{price}₴</div>
        {isOrderAvailable ? (
          <PaperBucketButton
            amount={count}
            onPlusClick={() => onAddToCart(product)}
            onMinusClick={() => onRemoveFromCart(product)}
            // onPlusClick={() => setAmount(amount + 1)}
            // onMinusClick={() => (amount > 0 ? setAmount(amount - 1) : null)}
          />
        ) : (
          <div className={classes.iconWrapper}>
            <img
              className={classes.warningIcon}
              alt='dish'
              src={WarningIconImg}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default ProductItem;
