import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { oneOfType, func, bool, element } from 'prop-types';
import { connect } from 'react-redux';

import { ROLES } from '../config';

SecretRoute.propTypes = {
  userLoggedIn: bool.isRequired,
  component: oneOfType([element, func]).isRequired,
};

function SecretRoute({ component: Component, user, requiredRoles, ...rest }) {
  return (
    <Route
      {...rest}
      render={(props) => {
        const rolesToCheck =
          requiredRoles &&
          (Array.isArray(requiredRoles) ? requiredRoles : [requiredRoles]);
        const checkPassed =
          user && (rolesToCheck ? rolesToCheck.includes(user.role) : true);

        return checkPassed ? <Component {...props} /> : <Redirect to='/auth' />;
      }}
    />
  );
}

const mapStateToProps = (state) => ({
  user: state.user.userData,
});

export default connect(mapStateToProps)(SecretRoute);
