import React from 'react';
import clsx from 'clsx';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

import { useMaterialInputLabels } from '../../../config';

import colors from '../../../themes';
import { lighten } from '../../../themes/utility';

const useStyles = makeStyles((theme) => ({
  container: {
    width: '300px',
    // margin: theme.spacing(1),
  },
  input: {
    // border: `1px solid ${colors.primaryLight}`,
    borderRadius: '4px',
    '&:hover': {
      borderColor: lighten(colors.secondary, 0.8),
    },
    '&:active': {
      outline: 'none',
      border: 'none',
      borderColor: 'red',
    },
    '&:focus': {
      outline: 'none',
      border: 'none',
      borderColor: 'red',
    },
  },
  customLabel: {
    marginLeft: '15px',
    marginBottom: '5px',
    fontSize: '14px',
  },
}));

const PaperInput = ({
  id,
  label,
  type,
  value,
  onChange,
  startAdornment,
  endAdornment,
  containerStyle,
  inputStyle,
  inputClass,
  labelWidth = 70,
}) => {
  const classes = useStyles();

  return (
    <FormControl
      className={clsx(classes.container, containerStyle)}
      variant='outlined'
    >
      {useMaterialInputLabels ? (
        <InputLabel htmlFor={id}>{label}</InputLabel>
      ) : (
        <label className={classes.customLabel}>{label}</label>
      )}
      <OutlinedInput
        id={id}
        type={type}
        value={value}
        onChange={onChange}
        startAdornment={startAdornment}
        endAdornment={endAdornment}
        className={clsx(classes.input, inputStyle)}
        labelWidth={useMaterialInputLabels ? labelWidth : undefined}
        classes={{ input: inputClass }}
      />
    </FormControl>
  );
};

export default PaperInput;
