import React, { Fragment, useState } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';

import NoProductImg from '../../img/no-product-photo.png';
import {
  getProductsList,
  setToCartRequest,
  removeFromCartRequest,
} from '../../store/actions';
import { getReadableProducts } from '../../store/selectors/products';
import { getProductsFromCart } from '../../store/selectors/cart';
import productsData from '../../common/menuData';
import PaperBackButton from '../../common/components/BackButton';
import ProductList from '../../common/components/ProductList';
import ProductItem from '../../common/components/ProductItem';
import { lighten } from '../../themes/utility';
import {
  getDeliveryTimeOptions,
  getProductsByCategory,
} from '../../utils/helpers';
import PaperSpinner from '../../common/components/Spinner';

import CartBlock from './components/CartBlock';

import useStyles from './styles';

function Products({
  user,
  match,
  productsList,
  productsFromCart,
  getProductsList,
  setToCartRequest,
  removeFromCartRequest,
  timeSlots,
  isLoading,
}) {
  const classes = useStyles();

  React.useEffect(() => {
    const { params: { id: vendorId } = {} } = match || {};
    getProductsList(vendorId);
  }, []);

  if (!user) {
    return <Redirect to='/auth' />;
  }

  if (isLoading) return <PaperSpinner />;

  const { label: closestDeliveryTimeOptionLabel } =
    getDeliveryTimeOptions(timeSlots).find(({ disabled }) => !disabled) || {};

  const availableProductsList = productsList.filter(({ available }) => true);
  // const computedProductsData = getProductsByCategory(availableProductsList);
  // const categories = Object.keys(computedProductsData);
  // console.log('availableProductsList', availableProductsList);
  const vendorName = (availableProductsList[0] || {}).vendorName;

  return (
    <div className={classes.page}>
      <PaperBackButton />
      {availableProductsList.length ? (
        <>
          <div className={classes.infoBar}>
            <Typography className={classes.title}>{vendorName} меню</Typography>
            <div className={classes.timeSlotBarWrapper}>
              <div className={classes.timeSlotBar}>
                {closestDeliveryTimeOptionLabel ? (
                  <>
                    <span className={classes.timeSlotText}>
                      Найближче вікно замовлень:
                    </span>
                    <span className={classes.timeSlotValue}>
                      до {closestDeliveryTimeOptionLabel}
                    </span>
                  </>
                ) : (
                  <span className={classes.timeSlotUnavailable}>
                    Замовлення недоступне
                  </span>
                )}
              </div>
            </div>
          </div>
          <div className={classes.content}>
            <CartBlock orders={productsFromCart} />
            <div className={classes.productsWrapper}>
              <ProductList
                // category={category}
                products={availableProductsList}
                onAddToCart={setToCartRequest}
                onRemoveFromCart={removeFromCartRequest}
              />
            </div>
          </div>
        </>
      ) : (
        <div className={classes.productsUnavailableWrapper}>
          <Typography className={classes.productsUnavailableTitle}>
            Немає доступних продуктів
          </Typography>
        </div>
      )}
    </div>
  );
}

const mapStateToProps = (state) => ({
  user: state.user.userData,
  productsList: getReadableProducts(state),
  productsFromCart: getProductsFromCart(state),
  timeSlots: state.timeSlots.list,
  isLoading: state.products.loading,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      getProductsList,
      setToCartRequest,
      removeFromCartRequest,
    },
    dispatch,
  ),
});

// export default withRouter(Products);
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withRouter(Products));
