import { getVendorList } from './vendor';
import { getProductsFromCart } from './cart';

export const getReadableProducts = (state) => {
  const vendorsByIdMap = getVendorList(state).reduce((acc, { id, name }) => {
    acc[id] = name;
    return acc;
  }, {});

  return state.products.list.map((product) => ({
    ...product,
    computedDescription: product.description
      ? [product.description, product.ingredients].join('\n')
      : product.ingredients,
    vendorName: vendorsByIdMap[product.vendorId],
    count: (
      getProductsFromCart(state).find(({ id }) => id === product.id) || {}
    ).count,
  }));
};
