import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PaperInput from '../../../../common/components/Input';
import PaperButton from '../../../../common/components/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import { text } from '../../../../config/text';

import PaperCheckbox from '../../../../common/components/Checkbox';

import colors from '../../../../themes';
import { lighten } from '../../../../themes/utility';

const useStyles = makeStyles(() => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  inputContainer: {
    marginBottom: '12px',
  },
  rememberMeWrapper: {
    display: 'flex',
    justifyContent: 'flex-start',
    width: '100%',
  },
  rememberMeContainer: {
    // marginTop: '200px',
  },
  rememberMeLabel: {
    fontSize: '14px',
    marginLeft: '5px',
    color: colors.secondary,
  },
  button: {
    minWidth: 300,
    marginTop: '29px',
    padding: '10px 0',
  },
  forgotPasswordBtn: {
    color: colors.primary,
    marginTop: '16px',
    cursor: 'pointer',
    textDecorationLine: 'underline',
    fontSize: '14px',
    border: 0,
    padding: 0,
    backgroundColor: 'transparent',
    '&:hover': {
      color: lighten(colors.primary, 0.8),
    },
    '&:active': {
      outline: 'none',
      border: 'none',
    },
    '&:focus': {
      outline: 'none',
      border: 'none',
    },
  },
}));

const LoginForm = ({ onSubmit, onForgotPassword }) => {
  const classes = useStyles();

  const [values, setValues] = useState({
    username: 'test7@gmail.com',
    password: 'test7',
    showPassword: false,
    rememberMe: false,
  });

  const { username, password, showPassword, rememberMe } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, [name]: event.target.value });
  };
  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };
  const handleClickRememberMe = () => {
    setValues({ ...values, rememberMe: !values.rememberMe });
  };

  const submitForm = (event) => {
    event.preventDefault();
    onSubmit(username, password, rememberMe);
  };

  const handleKeyUp = (event) => {
    if (event.keyCode === 13) return onSubmit(username, password);
  };

  return (
    <form
      className={classes.form}
      noValidate
      autoComplete='off'
      onSubmit={submitForm}
    >
      <PaperInput
        id='standard-name'
        label='Email/Tel'
        value={username}
        onChange={handleChange('username')}
        containerStyle={classes.inputContainer}
      />
      <PaperInput
        id='password'
        label='Password'
        type={showPassword ? 'text' : 'password'}
        value={password}
        onChange={handleChange('password')}
        endAdornment={
          <InputAdornment position='end'>
            <IconButton
              aria-label='toggle password visibility'
              onClick={handleClickShowPassword}
              edge='end'
            >
              {showPassword ? <Visibility /> : <VisibilityOff />}
            </IconButton>
          </InputAdornment>
        }
        containerStyle={classes.inputContainer}
      />
      <div className={classes.rememberMeWrapper}>
        <PaperCheckbox
          name={'rememberMe'}
          label={'Remember me'}
          containerClassName={classes.rememberMeContainer}
          labelClassName={classes.rememberMeLabel}
          checkboxColor={colors.greenLight}
          onChange={handleClickRememberMe}
        />
      </div>
      <PaperButton type='submit' className={classes.button}>
        {text.logIn.logInTitle}
      </PaperButton>
      <button className={classes.forgotPasswordBtn} onClick={onForgotPassword}>
        {text.logIn.forgotPassword}
      </button>
    </form>
  );
};

export default LoginForm;
