import { GET_VENDORS, GET_VENDORS_SUCCESS, GET_VENDORS_FAIL } from '../actions';

const initialState = {
  list: [],
  loading: false,
  error: null,
};

export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case GET_VENDORS:
      return { ...state, list: [], loading: true, error: null };
    case GET_VENDORS_SUCCESS:
      return { ...state, list: action.data, loading: false, error: null };
    case GET_VENDORS_FAIL:
      return { ...state, list: [], loading: false, error: action.error };
    default:
      return state;
  }
}
